.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #ff833e;
  padding: 1.33em 1.8em;
  font-size: 15px;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  letter-spacing: 0.066em;
  border-radius: 10px;
  text-align: center;
  vertical-align: top;
  position: relative;
  outline: none;
  transition: background-color .3s, box-shadow .3s;
  line-height: 1.4;
  cursor: pointer;
  box-shadow: 0 12px 20px -6px #ff833e;
}

.no-touch .btn:hover {
  background-color: #fd975e;
  box-shadow: none;
}

.btn_bold {
  text-transform: uppercase;
  font-weight: 700;
  padding: 1.4em 1.8em;
}

@media only screen and (max-width: 1023px) {
  .btn_bold {
    font-size: 10px;
    padding: 14px 20px;
  }
}

.btn_blue {
  background-color: #fff;
  box-shadow: none;
  color: #9d9d9d;
  transition: background-color .3s, box-shadow .3s, color .3s;
}

.btn_blue.is-active {
  color: #fff;
  background-color: #0053a5;
  box-shadow: 0 19px 25px -12px #0053a5;
}

.no-touch .btn_blue:hover {
  color: #fff;
  background-color: #0062C3;
  box-shadow: 0 19px 25px -12px #0053a5;
}

.btn_md {
  min-width: 280px;
}

.course {
  display: block;
  padding: 1.7em 2em;
  background-color: #fff;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 10px;
  transition: background-color .4s, color .4s, box-shadow .4s, transform .15s;
}

.no-touch .course:hover {
  background-color: #ff833e;
  color: #fff;
  box-shadow: 0 25px 42px -14px #ff833e;
  transform: translateY(-8px);
}

.no-touch .course:hover .course__aside {
  color: #fff;
}

.no-touch .course:hover .course__price:before {
  background-color: #fff;
}

.no-touch .course:hover .course__title.title {
  color: #fff;
}

.course__aside {
  color: #9d9d9d;
  transition: color .4s;
}

.course__bottom {
  display: flex;
  padding-top: 1em;
  font-size: 1.2em;
  font-weight: 700;
  letter-spacing: normal;
}

.course__price {
  margin-left: 30px;
  position: relative;
}

.course__price:before {
  content: '';
  position: absolute;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background-color: #000;
  left: -18.5px;
  top: 50%;
  margin-top: -3.5px;
  transition: background-color .4s;
}

.course__title {
  margin-top: .4em;
  transition: color .4s;
}

.event {
  display: block;
  color: #000;
  transition: transform .15s;
}

.no-touch .event:hover {
  transform: translateY(-8px);
}

.event__img img {
  max-width: 100%;
  display: block;
}

.event__date {
  font-size: 16px;
  line-height: 1.75;
  padding-top: 1.25em;
  letter-spacing: normal;
}

.event__date span {
  text-transform: uppercase;
  color: #ff833e;
  font-weight: 700;
}

.event__title {
  max-width: 300px;
  margin-top: 0.25em;
}

.instructor {
  background-color: #fff;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  overflow: hidden;
  transition: transform .15s;
}

.no-touch .instructor:hover {
  transform: translateY(-8px);
}

.instructor__top {
  background-color: #0053a5;
  padding-bottom: 34%;
  position: relative;
  overflow: hidden;
}

.instructor__top:before {
  content: '';
  position: absolute;
  top: 1.33em;
  left: 2em;
  right: 2em;
  bottom: -1em;
  border: 1px solid #fff;
  border-radius: 10px;
}

.instructor__bottom {
  text-align: center;
  padding: 0 2em 2.66em;
}

.instructor__bottom p {
  text-align: left;
  line-height: 1.9;
}

.instructor__img {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin: -5.7em auto 0;
  overflow: hidden;
  z-index: 1;
  position: relative;
}

.instructor__title {
  margin-top: 0.5em;
}

.instructor__aside {
  letter-spacing: 0.063em;
  color: #9d9d9d;
  margin-bottom: 1em;
}

.testimonial {
  background-color: #fff;
  padding: 2em 2em 2.66em;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  transition: transform .15s;
}

.no-touch .testimonial:hover {
  transform: translateY(-8px);
}

.testimonial__top {
  display: flex;
  align-items: center;
}

.testimonial__img {
  flex-shrink: 0;
  height: 90px;
  width: 90px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  overflow: hidden;
  margin-right: 1.33em;
}

.testimonial__aside {
  color: #9d9d9d;
  letter-spacing: 0.033em;
}

.testimonial__bottom {
  position: relative;
  margin-top: 1.6em;
}

.testimonial__bottom:before {
  content: '';
  position: absolute;
  width: 54px;
  height: 38px;
  background-image: url(../img/quotes.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
