.checkbox {
  position: relative;
  cursor: pointer;
}

.checkbox__icon {
  position: absolute;
  top: -5px;
  left: -39px;
  width: 25px;
  height: 25px;
  border-radius: 4px;
  border: 1px solid #fff;
}

.checkbox__text {
  display: inline-block;
  border-bottom: 1px solid transparent;
  transition: border-color .3s;
}

.no-touch .checkbox__text:hover {
  border-color: currentColor;
}

.checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox input:checked ~ .checkbox__icon {
  background-image: url(../img/check.svg);
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
