    display: flex;
    z-index: 1;
  }
}

.no-touch .header__toggle:hover .header__toggle-icon:before, .no-touch .header__toggle:hover .header__toggle-icon:after {
  width: 100%;
}

.header__toggle-icon {
  position: relative;
  width: 22px;
  height: 3px;
  background-color: #ff833e;
  border-radius: 1.5px;
}

.header__toggle-icon:before, .header__toggle-icon:after {
  content: '';
  position: absolute;
  width: 15px;
  height: 3px;
  right: 0;
  border-radius: 1.5px;
  background-color: #ff833e;
  transition: transform .3s, width .3s;
}

.header__toggle-icon:before {
  transform: translateY(-6px);
}

.header__toggle-icon:after {
  transform: translateY(6px);
}

.header.is-open .header__toggle-icon {
  background-color: transparent;
}

.header.is-open .header__toggle-icon:before {
  width: 100%;
  background-color: #ff833e;
  animation: open-before .5s linear both;
}

.header.is-open .header__toggle-icon:after {
  width: 100%;
  background-color: #ff833e;
  animation: open-after .5s linear both;
}

@keyframes open-before {
  0% {
  }
  40% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0) rotate(45deg);
  }
}

@keyframes open-after {
  0% {
  }
  40% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0) rotate(-45deg);
  }
}

