.footer {
	color: $white;
	padding: 5em 0;
	background-color: $footer;
	a {
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: currentColor; } }
	&__top {
		display: flex; }
	&__logo {
		max-width: 223px;
		margin-right: auto; }
	&__socials {
		margin-left: auto; }
	&__bottom {
		display: flex;
		justify-content: flex-end;
		padding-top: 3.15em; }
	&__info {
		display: flex;
		margin-right: 11.85em; }
	&__policy {
		&:not(:first-child) {
			margin-left: 2em; } } }


.socials {
	display: flex;
	flex-wrap: wrap;
	&__item {
		&:not(:last-child) {
			margin-right: 2em; }
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 20px;
			border: 2px solid currentColor;
			width: 2.5em;
			height: 2.5em;
			border-radius: 10px;
			transition: border-color .3s, color .3s;
			@include hover {
				border-color: $blueHover;
				color: $blueHover; } } }
	.icon-vk {
		width: 1.3em; } }
