.title {
	@include verdana;
	@include f(44px, 700, 1.23);
	color: $black;
	letter-spacing: normal;
	@include md {
		font-size: 30px; }
	&_lg {
		font-size: 30px;
		line-height: 1.48; }
	&_md {
		font-size: 21px;
		line-height: 1.48;
		@include md {
			font-size: 19px; } } }

