.about {
	padding: 144px 0 154px;
	position: relative;
	@include sm {
		padding: 60px 0 0; }
	&:before {
		content: '';
		position: absolute;
		top: 50px;
		left: 0;
		background-color: $blue;
		width: 42.4%;
		// padding-bottom: 37.4%
		height: 549px;
		border: 2px solid $white;
		border-left: none;
		box-shadow: 0 0 0px 50px $blue;
		z-index: -1;
		@include sm {
			display: none; } }
	&__inner {
		max-width: 1162px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		@include sm {
			flex-wrap: wrap; } }
	&__img {
		width: 50%;
		img {
			display: block;
			max-width: 100%; }
		@include sm {
			width: 100%; } }
	&__text {
		width: 50%;
		padding-left: 4em;
		@include md {
			padding-left: 2em; }
		@include sm {
			padding-left: 0;
			width: 100%; } }
	&__title {
		padding-bottom: 0.5em;
		margin-top: -20px;
		span {
			display: inline-block;
			position: relative;
			padding-right: 2.25em;
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				// margin-top: -3px
				right: 0;
				width: 1.86em; // 82px
				height: 6px;
				background-color: currentColor; } }
		@include sm {
			margin-top: 40px; } }
	&__dots {
		position: absolute;
		right: 28px;
		bottom: -107px; } }

.dots {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	max-width: 54px;
	width: 100%;
	&__item {
		width: 8px;
		height: 8px;
		margin: 4px 5px;
		background-color: $black; }
	@include sm {
		display: none; } }
