.header {
	box-shadow: 0 8px 65px -45px rgba(0, 0, 0, 0.5);
	position: relative;
	z-index: 1;
	&__absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		box-shadow: none;
		color: $white; }
	&__top {
		padding: 1.6em 0;
		text-transform: uppercase;
		opacity: .5;
		font-size: 10px;
		letter-spacing: 0.025em;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		@include md {
			display: none; }
		// a
		// 	border-bottom: 1px solid transparent
		// 	transition: border-color .3s
		// 	+hover
		// 		border-color: currentColor
		.header__absolute & {
			opacity: 1;
			padding: 0.8em 0; } }
	&__contacts {
		margin-left: 26px;
		display: flex; }
	&__phone {
		position: relative;
		&:not(:last-child) {
			padding-right: 45px; }
		&:after {
			content: '';
			position: absolute;
			@include circle(5);
			background-color: currentColor;
			top: 50%;
			margin-top: -2.5px;
			right: 20px; } }
	&__main {
		background-color: $white;
		padding: 1.5em 0;
		.inner {
			display: flex;
			align-items: center;
			flex-wrap: wrap; }
		.header__absolute & {
			background-color: transparent; }
		@include md {
			padding: 0; } }
	&__logo {
		margin-right: 1.5em;
		max-width: 80px;
		@include md {
			max-width: 56px; } }
	&__nav {
		padding-right: 1em; }
	&__group {
		display: flex;
		align-items: center;
		margin-left: auto; }
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 20px;
		opacity: 0.5;
		transition: opacity .3s;
		border: 2px solid currentColor;
		// padding: 0.75em
		width: 50px;
		height: 50px;
		border-radius: 10px;
		margin-right: 20px;
		@include hover {
			opacity: 1;
			border-color: $blueHover;
			.icon {
				color: $blueHover; } }
		.header__absolute & {
			opacity: 1; }
		@include lg {
			width: 40px;
			height: 40px;
			margin-right: 10px;
			font-size: 18px; }
		@include md {
			border: none;
			border-left: 1px solid #d8d8d8;
			border-right: 1px solid #d8d8d8;
			border-radius: 0;
			width: 82px;
			height: 60px;
			margin: 0; } }
	&__btn {
		.btn {
			letter-spacing: 0.062em;
			padding: 1em 1.8em;
			box-shadow: none;
			@include lg {
				font-size: 12px; } }
		@include md {
			display: none; } } }

.logo {
	flex-shrink: 0;
	a {
		display: block; }
	img {
		max-width: 100%;
		display: block; } }

.nav {
	@include md {
		display: none; }
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		position: relative;
		// z-index: 1
		transition: opacity .3s;
		opacity: 0.3;
		// &:before
		// 	content: ''
		// 	position: absolute
		// 	top: 100%
		// 	left: 0
		// 	width: 100%
		// 	height: 2px
		// 	background-color: currentColor
		// 	transform: scaleX(0)
		// 	transform-origin: right
		// 	transition: transform .5s
		&:not(:first-child) {
			margin-left: 2.7em;
			@include lg {
				margin-left: 1.5em; } }
		@include hover {
			opacity: 1;
			// &:before
			// 	transform: scaleX(1)
			// 	transform-origin: left
			.nav__sublist {
				opacity: 1;
				visibility: visible; } }
		&_current {
			opacity: 1; } }
	&__link {
		display: block;
		@include lg {
			font-size: 13px; }
		.header__absolute & {
			opacity: 1; } }
	&__sublist {
		position: absolute;
		top: 30px;
		left: -10px;
		padding: 0 10px 10px;
		opacity: 0;
		visibility: hidden;
		transition: opacity .3s, visibility .3s; }
	&__subitem {
		@include lg {
			font-size: 13px; } } }


// 									MOBILE
.header__toggle {
	cursor: pointer;
	display: none;
	align-items: center;
	// margin-left: auto
	width: 62px;
	height: 60px;
	padding: 0 12px 0 28px;
	@include md {
		display: flex;
		z-index: 1; }
	@include hover {
		.header__toggle-icon:before, .header__toggle-icon:after {
			width: 100%; } } }

.header__toggle-icon {
	position: relative;
	width: 22px;
	height: 3px;
	background-color: $orange;
	border-radius: 1.5px;
	&:before, &:after {
		content: '';
		position: absolute;
		width: 15px;
		height: 3px;
		right: 0;
		border-radius: 1.5px;
		background-color: $orange;
		transition: transform .3s, width .3s; }
	&:before {
		transform: translateY(-6px); }
	&:after {
		transform: translateY(6px); }
	.header.is-open & {
		background-color: transparent;
		&:before {
			width: 100%;
			background-color: $orange;
			animation: open-before .5s linear both; }
		&:after {
			width: 100%;
			background-color: $orange;
			animation: open-after .5s linear both; } } }
@keyframes open-before {
	0% {}

	40% {
		transform: translateY(0); }
	60% {
		transform: translateY(0); }
	100% {
		transform: translateY(0) rotate(45deg); } }
@keyframes open-after {
	0% {}

	40% {
		transform: translateY(0); }
	60% {
		transform: translateY(0); }
	100% {
		transform: translateY(0) rotate(-45deg); } }
